<template>
  <div
    class="container-fluid min-vh-100
  d-flex flex-column justify-content-center"
    id="login_bg"
  >
    <h3 class="h3 text-center text-white mb-4">請先登入</h3>
    <div class="row justify-content-center">
      <div class="col-10 col-md-8 col-lg-6">
        <form id="form" class="form-signin">
          <div class="form-floating mb-3">
            <input
              type="email"
              class="form-control rounded-pill px-3"
              id="username"
              v-model="user.username"
              placeholder="name@example.com"
              required
              autofocus
            />
            <label for="username">Email address</label>
          </div>
          <div class="form-floating">
            <input
              type="password"
              class="form-control rounded-pill px-3"
              id="password"
              v-model="user.password"
              placeholder="Password"
              required
            />
            <label for="password">Password</label>
          </div>
          <button
            class="btn btn-lg btn-primary text-white rounded-pill w-100 mt-3"
            @click="signIn"
            type="button"
          >
            登入
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<style>
#login_bg {
  background-image: linear-gradient(rgba(0,0,0,0.25), rgba(0,0,0,0)), url('@/assets/images/loginbg.jpg');
  background-size: cover;
}
</style>

<script>
export default {
  data () {
    return {
      user: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    signIn () {
      const url = `${process.env.VUE_APP_API}/admin/signin`
      this.$http
        .post(url, this.user)
        .then(res => {
          const { token, expired } = res.data
          document.cookie = `myToken = ${token}; expires = ${new Date(expired)}`
          this.$router.push('/admin/products')
        })
        .catch(err => {
          console.dir(err.response.data.message)
          //   alert(err.response.data.message)
        })
    }
  }
}
</script>
